import * as THREE from "three";
import gsap from "gsap";
import Experience from "../Experience.js";

export default class Laptop {
  constructor() {
    this.experience = new Experience();
    this.scene = this.experience.scene;
    this.resources = this.experience.resources;
    this.time = this.experience.time;
    this.objectsDistance = this.experience.objectsDistance;
    /* TEST */
    this.currentColor = this.experience.currentColor;
    this.currentSection = this.experience.currentSection;
    this.scroll = this.experience.scroll;
    this.sizes = this.experience.sizes;
    this.currentColor = this.experience.currentColor;
    /* END TEST */
    this.debug = this.experience.debug;

    if (this.debug.active) {
      this.debugFolder = this.debug.ui.addFolder("laptop");
    }

    // Setup
    this.resource = this.resources.items.laptopModel;

    this.resource.scene.traverse(function (child) {
      if (child.name === "Top") {
        child.rotation.x = "3.14";
      }
    });

    this.setModel();
  }

  update() {
    this.model = this.resource.scene;
    this.model.traverse((child) => {
      if (child instanceof THREE.Mesh) {
        child.castShadow = true;
        if (child.name !== "Circle002_4" && child.name !== "Circle_1") {
          child.material.color.setHex(`0x${this.currentColor.currentColor}`);
        }
      }
    });
    this.animate();
  }

  setModel() {
    this.model = this.resource.scene;
    this.model.position.y = -this.objectsDistance * 0.25;
    this.model.position.x = 0;
    this.scene.add(this.model);

    this.model.traverse((child) => {
      console.log(child);
      if (child.name === "FrontCameraRing001") {
        child.material.opacity = 0;
      }
      if (child instanceof THREE.Mesh) {
        child.castShadow = true;
        child.material.transparent = true;
      }
    });
  }

  animate() {
    const newSection = Math.round(this.scroll.y / this.sizes.height);

    if (newSection != this.currentSection) {
      this.currentSection = newSection;

      this.resource.scene.traverse(function (child) {
        if (child.name === "Top") {
          switch (newSection) {
            case 1:
              gsap.to(child.rotation, {
                duration: 1,
                ease: "power2.inOut",
                x: "1.3",
              });
              break;
            case 2:
              gsap.to(child.rotation, {
                duration: 1,
                ease: "power2.inOut",
                x: "2",
              });
              break;
            case 3:
              gsap.to(child.rotation, {
                duration: 1,
                ease: "power2.inOut",
                x: "1",
              });
              break;
            default:
              gsap.to(child.rotation, {
                duration: 1,
                ease: "power2.inOut",
                x: "3.14",
              });
          }
        }

        if (child.name === "Macbook") {
          switch (newSection) {
            case 0:
              gsap.to(child.rotation, {
                duration: 0.5,
                ease: "power2.inOut",
                y: "0",
              });
              break;
            case 1:
              gsap.to(child.rotation, {
                duration: 1,
                ease: "power2.inOut",
                y: "0",
              });
              break;
            case 2:
              gsap.to(child.rotation, {
                duration: 0.5,
                ease: "power2.inOut",
                y: "3.14",
              });
              break;
            case 3:
              gsap.to(child.rotation, {
                duration: 0.5,
                ease: "power2.inOut",
                y: "5.5",
              });
              break;
            default:
              gsap.to(child.rotation, {
                duration: 0.5,
                ease: "power2.inOut",
                y: "0",
              });
          }
        }
      });
    }
  }
}
